* {
  margin: 0;
  padding: 0;
}

body {
  background-image: url('./img/chuckNorris.jpg');
  background-size: 100vw 100vh;
  background-position: 0 10vh;
  background-repeat: no-repeat;
  color: white;
}

.navbar {
  height: 10vh;
  background-image: url('./img/walker.png');
  background-size: 10vw 10vh;
  background-color: #56424B !important;
}

.navbarText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50% , -50%);
  color: #fff;
  -webkit-text-stroke: 0.85px blue;
  font-size: 2em;
  font-weight: bold;
}

.navbar span {
  color: gold;
  -webkit-text-stroke: 0.85px blue;
}

.container {
  margin: 2em;
  margin-top: 10em;
  width: 38vw;
  text-align: center;
}

.container h1 {
  font-size: 3em;
  color: white;
  -webkit-text-stroke: 0.85px blue;
}

.container h1 span {
  
}

.container h2 {
  margin-bottom: 0.5em;
}

.container h3 {
  font-size: 1.5em;
  margin: 1.5em 0.5em 0.5em 0;
  color: gold;
}

button {
  background-image: url('./img/chuckKarate.jpg');
  background-size: 15vw 9vh;
  width: 15vw;
  height: 8vh;
  margin: 0.25em;
}

.btn {
  color: gold;
  -webkit-text-stroke: 0.85px blue;
  border: 1px solid gold;
  font-size: 1.25em;
  font-weight: 900;
  float: left;
}

.btn:hover {
  color: white;
  -webkit-text-stroke: 0.85px blue;
  border-color: blue;
}

.btnFact {
  float: none;
  margin-bottom: 1.5em;
}

.sidekicks {
  background-image: url('./img/sidekicks.jpg');
}

.octagon {
  background-image: url('./img/octagon.jpg');
}

.forceOfOne {
  background-image: url('./img/forceOfOne.jpg');
}

.missingInAction {
  background-image: url('./img/missingInAction.jpg');
}

.logansWar {
  background-image: url('./img/logansWar.jpg');
}

.hellbound {
  background-image: url('./img/hellbound.jpg');
}

iframe {
  display: none;
  width: 30vw;
  height: 30vh;
  border: 1px solid blue;
  position: fixed;
  top: 12em;
  right: 3em;
}

#wrapper {
  background-image: url('./img/movieScreen.jpg');
}

.display {
  display: block;
}


/* Tablets */
@media (min-width: 700px) and (max-width: 1024px) {
  .navbarText {
    font-size: 2.25em;
    position: relative;
    top: 25%;
    left: 50%;
    transform: translate(-50% , -50%);
  }

  .container h1 {
    font-size: 1.85em;
  }

  .container h2 {
    font-size: 1.5em;
  }

  .container h3 {
    font-size: 1.25em;
  }

  button {
    width: 20vw;
    height: 5vh;
    background-size: 20vw 5vh;
  }

  .btn {
    font-size: 1em;
    margin-top: 1.5em;
  }

  .btnFact {
    margin-top: 0;
    font-size: 0.9em;
  }

  .missingInAction {
    font-size: 0.9em;
  }

  iframe {
    right: 1.5em;
  }
}


/* Mobile Devices */
@media (max-width: 600px) {
  .navbarText {
      font-size: 1em;
      position: relative;
      top: 25%;
      left: 50%;
      transform: translate(-50% , -50%);
  }

  .container {
    margin: 0;
    margin-top: 6em;
    width: 45vw;
  }

  .container h1 {
    display: none;
  }

  .container h2 {
    display: none;
  }

  .container h3 {
    font-size: 0.75em;
  }

  button {
    width: 30vw;
    height: 5vh;
    background-size: 30vw 5vh;
  }

  .btn {
    font-size: 0.75em;
    margin-top: 1.5em;
  }

  .btnFact {
    margin-top: 0;
    font-size: 0.5em;
    width: 28vw;
    height: 5vh;
    background-size: 28vw 5vh;
    padding: 0.9em;
    float: right;
    position: relative;
    bottom: 5.5em;;
    left: 21.5em;
  }

  iframe {
    width: 90vw;
    height: 30vh;
    position: absolute;
    transform: translate(1.85em, 16em);
  }

  .gridContainer {
    display: grid;
    grid-template-columns: auto auto;
  }

  .octagon {
    position: relative;
    left: 8.5em;
  }

  .missingInAction {
    position: relative;
    left: 8.5em;
    padding: 0.25em;
  }

  .hellbound {
    position: relative;
    left: 8.5em;
  }
}

/* XS Mobile */

@media (max-width: 355px) {

  .navbarText {
    font-size: 0.9em;
}

  .btn {
    font-size: 0.6em;
  }

  .btnFact {
    padding: 0.3em;
    font-size: 0.5em;
    left: 20em;
  }

  .octagon {
    position: relative;
    left: 10em;
  }

  .missingInAction {
    position: relative;
    left: 10em;
    padding: 0.25em;
  }

  .hellbound {
    position: relative;
    left: 10em;
  }

  iframe {
    width: 90vw;
    height: 30vh;
    position: absolute;
    transform: translate(2em, 10em);
  }
}